@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
.Desk {
  position: relative;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
  width: 200px;
  height: 200px;
  word-wrap: break-word;
  padding: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.2s ease;
  margin: 20px;
}

.Desk:hover {
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.1);
  cursor: pointer;
}

.Desk__delete {
  z-index: 5000;
  position: absolute;
  top: 10px;
  right: 10px;
  font-weight: bold;
  color: rgb(194, 57, 57);
  cursor: pointer;
}

.CreateDesk {
  color: #fff;
  background-color: rgba(207, 46, 46, 0.9);
  width: 200px;
  height: 200px;
  word-wrap: break-word;
  padding: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.2s ease;
  margin: 20px;
}

.CreateDesk:hover {
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.1);
  cursor: pointer;
}

.CreateShowDesk {
  color: #fff;
  background-color: rgba(207, 46, 46, 0.9);
  width: 200px;
  height: 200px;
  word-wrap: break-word;
  padding: 10px;

  display: flex;
  flex-direction: column;
  transition: transform 0.2s ease;
  margin: 20px;
}

.CreateShowDesk input {
  margin-top: 20px;
  margin-bottom: 20px;
  background: transparent;
  border: 0;
  color: white;
}

.CreateShowDesk button {
  margin-top: 10px;
  border: 0;
  border-radius: 50px;
  padding: 2px;
}

.CreateShowDesk button:hover {
  cursor: pointer;
}

.DeskList {
  max-width: 1000px;
  margin: 50px auto;
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 750px) {
  .DeskList {
    max-width: 1000px;
    margin: 50px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.Column {
  color: #fff;
  width: 100%;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.6);
  margin: 10px;
  transition: transform 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Column:hover {
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.1);
  cursor: pointer;
}

.Column.create:hover {
  background-color: rgba(207, 46, 46, 0.9);
}

.Column.Form {
  background-color: rgba(207, 46, 46, 0.9);
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 50%;
  margin: 50px auto;
  height: 200px;
}

.Column.Form input {
  max-width: 40%;
  border: none;
  background: transparent;
  color: white;
  border-bottom: 2px solid white;
  margin-bottom: 20px;
}

.Column.Form button {
  border: none;
  width: 150px;
  max-width: 50%;
  height: 40px;
  margin-top: 10px;
  cursor: pointer;
}

.ColumnList {
  max-width: 1000px;
  margin: 50px auto;
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 750px) {
  .ColumnList {
    max-width: 1000px;
    margin: 50px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.App {
  text-align: center;
  width: 100vw;
  min-height: 100vh;
  padding: 20px;
}

* {
  box-sizing: border-box;
  outline: none;
}

body {
  background-image: url('https://images.pexels.com/photos/1287145/pexels-photo-1287145.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
  background-size: cover;
  margin: 0;
  font-family: 'Muli', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

